.overlay {
  width: 100%;
  height: 100vh;
  background-color: rgba(45, 45, 45, 0.5);
  position: fixed;
  top: 0; }

.react-modal-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  height: 100vh;
  position: fixed;
  top: 0;
  z-index: 999; }
  @media screen and (min-width: 768px) {
    .react-modal-container {
      width: 50%;
      max-width: 500px;
      height: 30vh;
      position: fixed;
      top: 50%;
      left: 50%;
      border-radius: 20px;
      transform: translate(-50%, -50%);
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 2% 0; } }
  .react-modal-container__title {
    text-align: center;
    color: #2d2d2d;
    font-size: 2rem;
    margin-bottom: 1rem; }
  .react-modal-container__button-container {
    padding: 0 1rem; }
    @media screen and (min-width: 768px) {
      .react-modal-container__button-container {
        padding-bottom: 1rem;
        display: flex;
        justify-content: flex-end;
        margin: 0 auto; } }
  .react-modal-container__button {
    width: 100%;
    background-color: #2d2d2d;
    color: white;
    border-radius: 20px;
    margin-bottom: 1rem;
    font-size: 1.6rem; }
    @media screen and (min-width: 768px) {
      .react-modal-container__button {
        width: auto;
        padding: 0.5rem 2.5rem;
        margin: 0 1rem;
        display: flex;
        justify-content: flex-end; } }
